import { useEffect, useState } from "react";
import LinkImg from "../link.webp";
import KakaoImg from "../kakao.webp";

const { Kakao } = window;

const Share = () =>{
    const imageUrl = "https://raw.githubusercontent.com/sindy970/sindy970.github.io/refs/heads/main/src/img/ber.jpg";
    const resultUrl = window.location.href;

    //   .
    useEffect(()=>{
        // init   clean up  .
        Kakao.cleanup();
        //  js  .
        Kakao.init('8e920bbf27080912d8dc732f62f6cd1a');
    },[]);

    const shareKakao = () =>{

        Kakao.Share.sendDefault({
            objectType: 'feed',
            content: {
                title: ' ~',
                description: '   ~',
                imageUrl: imageUrl,
                link: {
                    mobileWebUrl: resultUrl,
                },
            },
            buttons: [
                {
                    title: ' ',
                    link: {
                        mobileWebUrl: resultUrl,
                    },
                },
            ],
        });
    }

    const handleCopyUrl = () => {
            navigator.clipboard.writeText(resultUrl)
            .then(() => {
                alert(' ');
            })
            .catch((error) => {
                console.error('  !');
            });
    };

    return(
        <>
            <div className="sns-area">
                <div className="sns-kakao">
                    <button type="button" className='grey-btn'
                            onClick={() => {
                                shareKakao()
                            }}><img src={KakaoImg} />
                    </button>
                </div>
                <div className="sns-url">
                    <button type="button" onClick={handleCopyUrl} ><img src={LinkImg} />
                    </button>
                </div>
            </div>
        </>
    )
}

export default Share;
